import firebase from "./services/firebase";
import React, { Component, useState } from "react";
import Slider, { Range } from 'rc-slider';
import 'rc-slider/assets/index.css';

import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Button,
  Container,
  Row,
  Col
} from "react-bootstrap";


const db = firebase.collection("/BGU-lab-dev");

class PersonalDetails extends Component{

  constructor(props) {
    super(props);
    this.state ={
      testCouner: 0,
      sliderValue: 0,
      start:false,
      lastDocID:"",
      firstName:"",
      lastName:"",
      saved:false,
      age:"",
      phone:"",
      email:"",
      Hand:"",
      Gender:"",
      sport:"",
      sport_duration:"",
      SportTimes:"",
      notes:"",
      

    }


    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleOptionHand = this.handleOptionHand.bind(this);
    this.handleOptionGender = this.handleOptionGender.bind(this);
    this.handleOptionPills = this.handleOptionPills.bind(this);

    this.handleOptionSport = this.handleOptionSport.bind(this);

    this.handleOptionSportTimes = this.handleOptionSportTimes.bind(this);



    

  }
  componentDidMount() {
    this.unsubscribe = firebase.collection('/slider-data').onSnapshot(this.onCollectionUpdate) 
    this.setState({sliderValue:0})

  }

  componentWillUnmount() {
    // we have to unsubscribe when component unmounts, because we don't need to check for updates
    this.unsubscribe()
  }

  
  onCollectionUpdate = (querySnapshot) => {
    // we have to update the state
    let value = 0;
    querySnapshot.forEach((document) => {
      const doc= document.data();
      console.log(doc)
      value = doc.value
    })
    this.setState({value:value, sliderValue:value})

  }



  getData() {
    let value;
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).get().
        then( docSnapshot => {
          [docSnapshot].forEach( doc => {
            const data = doc.data();
            console.log(doc.id, data);
            console.log(`sliderID: ${data.id}, value: ${data.value}`)
            value =  data.value;
          })

        })
        //console.log(`id: ${childSnapshot.id}, data: ${childSnapshot.data}`)
      })
    })
    console.log(value)
    return value
  }






  update( input) {
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).update({value:input})

      })
    })
  }




 
    


  onSliderChange = value => {
    this.setState(
      {
        sliderValue:value
      },
      () => {
        console.log(this.state.sliderValue);
      }
    );
  };


  
  handleChange(event) {
    const value = event.target.value;
    const name = event.target.name
    let update =  {}
    update[name] = value
    this.setState(update);
  }

  async handleSubmit(event) {
    event.preventDefault();
    db.orderBy('time', 'desc').limit(1).get().
            then((data) =>{
              const docRef = data.docs[0];
              const docData = docRef.data()
              const docID = docRef.id
              // const dataUpdate = {firstName: this.state.firstName, lastName:this.state.lastName}
              const docRefUpdate = db.doc(docRef.id);
              let updateFields = ['age','phone','email','Hand','Gender','period','Pills',
                  ,'sport','sport_duration','SportTimes','notes']
              let fieldsData = updateFields.reduce( (acc,curr) => 
                    this.state[curr] != "" ? acc.concat({[curr]: this.state[curr]}) : acc
                        , [])
              console.log({fieldsData})
              docRefUpdate.update({firstName: this.state.firstName, lastName:this.state.lastName, personalDetails:fieldsData})
              // console.log('updating doc with: ' +JSON.stringify(fieldsData) )
              this.setState({saved:true})
            })
  }

  handleOptionHand(e) {
    this.setState({
      Hand: e.target.value
    });
  }

  
  handleOptionGender(e) {
    this.setState({
      Gender: e.target.value
    });
  }
  
  handleOptionPills(e) {
    this.setState({
      Pills: e.target.value
    });
  }

  handleOptionSport(e) {
    this.setState({
      Sport: e.target.value
    });
  }

  
  handleOptionSportTimes(e) {
    this.setState({
      SportTimes: e.target.value
    });
  }

  


  render(){
    let sliderHeight = 200;
    return (
        <Row dir={"rtl"}>
          <Col>
          <form onSubmit={this.handleSubmit}>

            <Row>
                <Col>

                <label>
                שם פרטי:

                <input name ={"firstName"} value={this.state.firstName} onChange={this.handleChange} />
                </label>

                </Col>
            </Row>

            <Row>
                <Col>
                <label>
                שם משפחה:
                <input name = {"lastName"} value={this.state.lastName} onChange={this.handleChange} />
                </label> 
                </Col>
            </Row>

            <Row>
                <Col>
                <label>
                גיל
                <input name = {"age"} value={this.state.age} onChange={this.handleChange} />
                </label> 
                </Col>
            </Row>

            <Row>
                <Col>
                <label>
                טלפון
                <input name = {"phone"} value={this.state.phone} onChange={this.handleChange} />
                </label> 
                </Col>
            </Row>

            <Row>
                <Col>
                <label>
                  מייל

                <input name = {"email"} value={this.state.email} onChange={this.handleChange} />
                </label> 
                </Col>
            </Row>


            <Row>
                <Col>
                    יד דומיננטית
                      <Row  className="justify-content-md-center">
                                  <Col xs lg="2">
                                  <label>
                                  <input type='radio' name = {"right"} 
                                      value="ימין" 
                                      checked={this.state.Hand === "ימין"} 
                                      onChange={this.handleOptionHand} />
                                      ימין
                                  </label>
                                  
                                  </Col>

                                  <Col xs lg="2">
                                  <label>
                                  <input type='radio' name = {"left"} 
                                  value="שמאל" 
                                  checked={this.state.Hand === 'שמאל'} 
                                  onChange={this.handleOptionHand}/>
                                      שמאל
                                  </label>
                                  </Col>
                          </Row>
                    </Col>
              </Row>

              <Row>
                <Col>
                   גבר / אישה
                      <Row  className="justify-content-md-center">
                                  <Col xs lg="2">
                                  <label>
                                  <input type='radio' name = {"male"} 
                                      value="גבר" 
                                      checked={this.state.Gender === "גבר"} 
                                      onChange={this.handleOptionGender} />
                                      גבר
                                  </label>
                                  
                                  </Col>

                                  <Col xs lg="2">
                                  <label>
                                  <input type='radio' name = {"female"} 
                                  value="אישה" 
                                  checked={this.state.Gender === 'אישה'} 
                                  onChange={this.handleOptionGender}/>
                                      אישה
                                  </label>
                                  </Col>
                          </Row>
                    </Col>
              </Row>


              {
                this.state.Gender === "אישה" ?
                <div>

                <Row>
                  <Col>
                  <label>
                    כמה ימים את מהמחזור האחרון?

                  <input name = {"period"} value={this.state.period} onChange={this.handleChange} />
                  </label> 
                  </Col>
              </Row>


              <Row>
                <Col>
                נוטלת גלולות?
                      <Row  className="justify-content-md-center">
                                  <Col xs lg="2">
                                  <label>
                                  <input type='radio' name = {"pills-yes"} 
                                      value="נוטלת_גלולות" 
                                      checked={this.state.Pills === "נוטלת_גלולות"} 
                                      onChange={this.handleOptionPills} />
                                      כן
                                  </label>
                                  
                                  </Col>

                                  <Col xs lg="2">
                                  <label>
                                  <input type='radio' name = {"pills-no"} 
                                  value="לא_נוטלת_גלולות" 
                                  checked={this.state.Pills === 'לא_נוטלת_גלולות'} 
                                  onChange={this.handleOptionPills}/>
                                      לא
                                  </label>
                                  </Col>
                          </Row>
                    </Col>
              </Row>


              </div>


                :null
              }

            <Row> 
                <Col>
                  האם אתם פעילים גופנית?
                      <Row  className="justify-content-md-center">
                                  <Col xs lg="2">
                                  <label>
                                  <input type='radio' name = {"sports_active"} 
                                      value="פעיל_גופנית" 
                                      checked={this.state.Sport === "פעיל_גופנית"} 
                                      onChange={this.handleOptionSport} />
                                      כן
                                  </label>
                                  
                                  </Col>

                                  <Col xs lg="2">
                                  <label>
                                  <input type='radio' name = {"sports_not_active"} 
                                  value="לא_פעיל_גופנית" 
                                  checked={this.state.Sport === 'לא_פעיל_גופנית'} 
                                  onChange={this.handleOptionSport}/>
                                      לא
                                  </label>
                                  </Col>
                          </Row>
                    </Col>
              </Row>

              {
                this.state.Sport === "פעיל_גופנית" ?
                <div>
                  <Row>
                      <Col>
                      <label>
                        איזו פעילות אתם עושים?

                      <input name = {"sport"} value={this.state.sport} onChange={this.handleChange} />
                      </label> 
                      </Col>
                  </Row>

                  <Row>
                      <Col>
                      <label>
                        כמה זמן כל פעם (בממוצע)?

                      <input name = {"sport_duration"} value={this.state.sport_duration} onChange={this.handleChange} />
                      </label> 
                      </Col>
                  </Row>


                  
              <Row> 
                <Col>
                כמה פעמים בשבוע
                      <Row  className="justify-content-md-center">
                                  <Col xs lg="1">
                                  <label>
                                  <input type='radio' name = {"day1"} 
                                      value="פעם1" 
                                      checked={this.state.SportTimes === "פעם1"} 
                                      onChange={this.handleOptionSportTimes} />
                                      1
                                  </label>
                                  
                                  </Col>

                                  <Col xs lg="1">
                                  <label>
                                  <input type='radio' name = {"day2"} 
                                      value="פעמים2" 
                                      checked={this.state.SportTimes === "פעמים2"} 
                                      onChange={this.handleOptionSportTimes} />
                                      2
                                  </label>
                                  
                                  </Col>

                                  <Col xs lg="1">
                                  <label>
                                  <input type='radio' name = {"day3"} 
                                      value="פעמים3" 
                                      checked={this.state.SportTimes === "פעמים3"} 
                                      onChange={this.handleOptionSportTimes} />
                                      3
                                  </label>
                                  
                                  </Col>

                                  <Col xs lg="1">
                                  <label>
                                  <input type='radio' name = {"day4"} 
                                      value="פעמים4" 
                                      checked={this.state.SportTimes === "פעמים4"} 
                                      onChange={this.handleOptionSportTimes} />
                                      4
                                  </label>
                                  
                                  </Col>

                                  <Col xs lg="1">
                                  <label>
                                  <input type='radio' name = {"day5"} 
                                      value="פעמים5" 
                                      checked={this.state.SportTimes === "פעמים5"} 
                                      onChange={this.handleOptionSportTimes} />
                                      5
                                  </label>
                                  
                                  </Col>

                                  <Col xs lg="1">
                                  <label>
                                  <input type='radio' name = {"day6"} 
                                      value="פעמים6" 
                                      checked={this.state.SportTimes === "פעמים6"} 
                                      onChange={this.handleOptionSportTimes} />
                                      6
                                  </label>
                                  
                                  </Col>

                                  <Col xs lg="1">
                                  <label>
                                  <input type='radio' name = {"day7"} 
                                      value="פעמים7" 
                                      checked={this.state.SportTimes === "פעמים7"} 
                                      onChange={this.handleOptionSportTimes} />
                                      7
                                  </label>
                                  
                                  </Col>

                          </Row>

                          <Row>
                      <Col>
                        הערות

                      <textarea style={{width:"50%"}} name = {"notes"} value={this.state.notes} onChange={this.handleChange} />
                      </Col>
                  </Row>
                    </Col>
              </Row>


                </div>

                :null
              }








            {this.state.saved? <h6>נשמר</h6> : null}

            <input type="submit" value="Submit" />

            </form>


                


            </Col>
        </Row>

    )
  }
}

export default PersonalDetails;