import firebase from "./services/firebase";
import React, { Component, useState } from "react";
import Slider, { Range } from 'rc-slider';
import 'rc-slider/assets/index.css';

import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Button,
  Container,
  Row,
  Col
} from "react-bootstrap";


const db = firebase.collection("/BGU-lab-dev");

class CommunicationPage extends Component{

  constructor(props) {
    super(props);
    this.state ={
      sliderValue: 0,
      sliderValue2: 0,
      sliderValue3: 0,
      sliderValue4: 0,
      sliderValue5: 0,


      saved:false,
      start:false,
      q1:"",
      q2:"",
      q3:"",
      selectedOption:"",
      selectedOption2:"",
      selectedOption3:"",
      selectedOption4:"",



    }

    this.start = this.start.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);






    

  }
  componentDidMount() {
    this.unsubscribe = firebase.collection('/slider-data').onSnapshot(this.onCollectionUpdate) 
    this.setState({sliderValue:0})

  }

  componentWillUnmount() {
    // we have to unsubscribe when component unmounts, because we don't need to check for updates
    this.unsubscribe()
  }

  
  onCollectionUpdate = (querySnapshot) => {
    // we have to update the state
    let value = 0;
    querySnapshot.forEach((document) => {
      const doc= document.data();
      console.log(doc)
      value = doc.value
    })
    this.setState({value:value, sliderValue:value})

  }



  getData() {
    let value;
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).get().
        then( docSnapshot => {
          [docSnapshot].forEach( doc => {
            const data = doc.data();
            console.log(doc.id, data);
            console.log(`sliderID: ${data.id}, value: ${data.value}`)
            value =  data.value;
          })

        })
        //console.log(`id: ${childSnapshot.id}, data: ${childSnapshot.data}`)
      })
    })
    console.log(value)
    return value
  }






  update( input) {
    db.get().then(function(snapshot) {
      snapshot.forEach( function(childSnapshot){
        db.doc(childSnapshot.id).update({value:input})

      })
    })
  }




 
    


  onSliderChange = value => {
    this.setState(
      {
        sliderValue:value
      }
    );
  };
  onSliderChange2 = value => {
    this.setState(
      {
        sliderValue2:value
      }
    );
  };

  onSliderChange3 = value => {
    this.setState(
      {
        sliderValue3:value
      }
    );
  };
  onSliderChange4 = value => {
    this.setState(
      {
        sliderValue4:value
      }
    );
  };
  onSliderChange5 = value => {
    this.setState(
      {
        sliderValue5:value
      }
    );
  };


  
  handleChange(event) {
    const value = event.target.value;
    const name = event.target.name
    let update =  {}
    update[name] = value
    this.setState(update);
  }

  async handleSubmit(event) {
    event.preventDefault();
    db.orderBy('time', 'desc').limit(1).get().
            then((data) =>{
              const docRef = data.docs[0];
              const docData = docRef.data()
              const docID = docRef.id
              const dataUpdate = {
                  "1-האם הרגשת טוב עם פארו": this.state.sliderValue,
                 "2-האם נעים לגעת בפארו":this.state.sliderValue2,
                 "3-האם הרגשת שאתה יכול לתקשר עם פארו": this.state.sliderValue3,
                 "4-האם פארו עזר להפחית את הכאב":this.state.sliderValue4,
                 "5-האם תרצה לפגוש את פארו שוב":this.state.sliderValue5,

                }

              const docRefUpdate = db.doc(docRef.id);
            //   docRefUpdate.update(dataUpdate)
              docRefUpdate.update({communication:dataUpdate})
              // console.log('updating doc with: ' +JSON.stringify(dataUpdate) )
              this.setState({saved:true})
            })
 

  }

  

  
  start(event) {
    event.preventDefault();
    //this.setState({sliderValue:0})
    this.setState({sliderValue:0, start:true})

  }

  render(){
    let sliderHeight = 200;
    return (
        <Row>

          <Col>
          {
          !this.state.start ?  
                    <Button onClick ={this.start}>
                    התחל
                  </Button>
          :


        <Row>
            <Col>

           
            <Row>
                <Col>
                    <h3>שאלון תקשורת</h3>
                </Col>
            </Row>

        
            
            <form onSubmit={this.handleSubmit}>

      
                <Row style={{height:"50px"}}>
                    <Col>
                    </Col>
                </Row>
   
                <Row>
                    <Col>

                    הרגשת טוב עם PARO?


           
                        
                    </Col>
                </Row>

                
              <Row>
                    <Col xs={2} >
                    <h6 style={{margin:"0px"}}>  כלל לא </h6>
                    </Col>

                    <Col xs={8}>

                    </Col>
                    
                    <Col xs={2} style={{marginRight:"0px", paddingLeft:"0px"}}>
                      <h6 >במידה רבה</h6>
                    </Col>
              </Row>

                
                <Row className="justify-content-md-center" style ={{height:"200px"}}>
  

                        <Col >
                        
                                <Slider
                                    disabled = {this.state.saved}

                                    min={0}
                                    max={10}
                                    defaultValue={0}
                                    value={this.state.sliderValue}
                                    onChange={this.onSliderChange}
                                    step ={0.1}
                                    reverse = {true}
                                    railStyle={{ backgroundColor: 'grey', height: sliderHeight }}
                                    trackStyle={{ backgroundColor: 'red', height: sliderHeight }}
                                    handleStyle={{
                                        borderColor: 'grey',
                                        height: sliderHeight,
                                        width: 1,
                                        marginLeft: -14,
                                        marginTop: 0,
                                        backgroundColor: 'white',
                                    }} />
                        
                        </Col>

                    </Row>

                    <Row style={{height:"50px"}}>
                        <Col>
                        </Col>
                    </Row>

                    <Row>
                    <Col>
                          האם נעים לגעת ב PARO?

                    </Col>
                </Row>

                
                <Row>
                    <Col xs={2} >
                    <h6 style={{margin:"0px"}}>  כלל לא </h6>
                    </Col>

                    <Col xs={8}>

                    </Col>
                    
                    <Col xs={2} style={{marginRight:"0px", paddingLeft:"0px"}}>
                      <h6 >במידה רבה</h6>
                    </Col>
              </Row>

                
                <Row className="justify-content-md-center" style ={{height:"200px"}}>
  

                        <Col >
                        
                                <Slider
                                disabled = {this.state.saved}
                                    min={0}
                                    max={10}
                                    defaultValue={0}
                                    value={this.state.sliderValue2}
                                    onChange={this.onSliderChange2}
                                    
                                    step ={0.1}
                                    reverse = {true}
                                    railStyle={{ backgroundColor: 'grey', height: sliderHeight }}
                                    trackStyle={{ backgroundColor: 'red', height: sliderHeight }}
                                    handleStyle={{
                                        borderColor: 'grey',
                                        height: sliderHeight,
                                        width: 1,
                                        marginLeft: -14,
                                        marginTop: 0,
                                        backgroundColor: 'white',
                                    }} />
                        
                        </Col>

                    </Row>

                    <Row style={{height:"50px"}}>
                        <Col>
                        </Col>
                    </Row>

                    <Row>
                    <Col>
                                  האם הרגשת שאתה יכול לתקשר עם PARO?                   
                    </Col>
                </Row>

                
                <Row>
                    <Col xs={2} >
                    <h6 style={{margin:"0px"}}>  כלל לא </h6>
                    </Col>

                    <Col xs={8}>

                    </Col>
                    
                    <Col xs={2} style={{marginRight:"0px", paddingLeft:"0px"}}>
                      <h6 >במידה רבה</h6>
                    </Col>
              </Row>

                
                <Row className="justify-content-md-center" style ={{height:"200px"}}>
  

                        <Col >
                        
                                <Slider
                                disabled = {this.state.saved}
                                    min={0}
                                    max={10}
                                    defaultValue={0}
                                    value={this.state.sliderValue3}
                                    onChange={this.onSliderChange3}
                                    step ={0.1}
                                    reverse = {true}
                                    railStyle={{ backgroundColor: 'grey', height: sliderHeight }}
                                    trackStyle={{ backgroundColor: 'red', height: sliderHeight }}
                                    handleStyle={{
                                        borderColor: 'grey',
                                        height: sliderHeight,
                                        width: 1,
                                        marginLeft: -14,
                                        marginTop: 0,
                                        backgroundColor: 'white',
                                    }} />
                        
                        </Col>

                    </Row>

                    <Row style={{height:"50px"}}>
                        <Col>
                        </Col>
                    </Row>

                    <Row>
                    <Col>
                                    האם PARO עזר להפחית את הכאב?
                   
                    </Col>
                </Row>

                
                <Row>
                    <Col xs={2} >
                    <h6 style={{margin:"0px"}}>  כלל לא </h6>
                    </Col>

                    <Col xs={8}>

                    </Col>
                    
                    <Col xs={2} style={{marginRight:"0px", paddingLeft:"0px"}}>
                      <h6 >במידה רבה</h6>
                    </Col>
              </Row>

                
                <Row className="justify-content-md-center" style ={{height:"200px"}}>
  

                        <Col >
                        
                                <Slider
                                disabled = {this.state.saved}
                                    min={0}
                                    max={10}
                                    defaultValue={0}
                                    value={this.state.sliderValue4}
                                    onChange={this.onSliderChange4}
                                    step ={0.1}
                                    reverse = {true}
                                    railStyle={{ backgroundColor: 'grey', height: sliderHeight }}
                                    trackStyle={{ backgroundColor: 'red', height: sliderHeight }}
                                    handleStyle={{
                                        borderColor: 'grey',
                                        height: sliderHeight,
                                        width: 1,
                                        marginLeft: -14,
                                        marginTop: 0,
                                        backgroundColor: 'white',
                                    }} />
                        
                        </Col>

                    </Row>

                    <Row style={{height:"50px"}}>
                        <Col>
                        </Col>
                    </Row>

                    
                    <Row>
                    <Col>

                    האם תרצה לפגוש את PARO שוב?
                     
                    </Col>
                </Row>

                
                <Row>
                    <Col xs={2} >
                    <h6 style={{margin:"0px"}}>  כלל לא </h6>
                    </Col>

                    <Col xs={8}>

                    </Col>
                    
                    <Col xs={2} style={{marginRight:"0px", paddingLeft:"0px"}}>
                      <h6 >במידה רבה</h6>
                    </Col>
              </Row>

                
                <Row className="justify-content-md-center" style ={{height:"200px"}}>
  

                        <Col >
                        
                                <Slider
                                disabled = {this.state.saved}
                                    min={0}
                                    max={10}
                                    defaultValue={0}
                                    value={this.state.sliderValue5}
                                    onChange={this.onSliderChange5}
                                    step ={0.1}
                                    reverse = {true}
                                    railStyle={{ backgroundColor: 'grey', height: sliderHeight }}
                                    trackStyle={{ backgroundColor: 'red', height: sliderHeight }}
                                    handleStyle={{
                                        borderColor: 'grey',
                                        height: sliderHeight,
                                        width: 1,
                                        marginLeft: -14,
                                        marginTop: 0,
                                        backgroundColor: 'white',
                                    }} />
                        
                        </Col>

                    </Row>

                    <Row style={{height:"50px"}}>
                        <Col>
                        </Col>
                    </Row>
   

   

                



                <Row style={{height:"50px"}}>
                    <Col>
                    {this.state.saved? <h6>נשמר</h6> : null}

                    </Col>
                </Row>


           




                <input type="submit" value="Submit" />

                </form>


                

                </Col>
            </Row>
            }
            </Col>
        </Row>

    )
  }
}

export default CommunicationPage;